.loader {
  position: absolute;
  display: block;
  flex-direction: column;
  align-items: center;

  font-weight: 700;
  font-style: normal;
  font-size: 32px;
  color: rgb(59, 45, 90);
  text-align: center;

  width: 700px;
  height: 100px;
  margin-top: 50px;
  margin-left: 100px;
}

.loader::before {
  content: '';
  width: 15px;
  height: 15px;
  border-radius: 50%;
  display: block;
  margin: 15px auto;
  position: relative;
  color: rgb(59, 45, 90);
  box-sizing: border-box;
  animation: animloader 1s linear infinite alternate;
}

@keyframes animloader {
  0% {
    box-shadow: -38px -12px, -14px 0, 14px 0, 38px 0;
  }
  33% {
    box-shadow: -38px 0px, -14px -12px, 14px 0, 38px 0;
  }
  66% {
    box-shadow: -38px 0px, -14px 0, 14px -12px, 38px 0;
  }
  100% {
    box-shadow: -38px 0, -14px 0, 14px 0, 38px -12px;
  }
}

.loader::after {
  content: '';
  display: block;

  width: 700px;
  height: 700px;
  background-image: url(https://papik.pro/grafic/uploads/posts/2023-04/1682206914_papik-pro-p-stikeri-rizhii-kot-vektor-26.png);
  background-repeat: no-repeat;
  margin-top: 80px;
  margin-left: 100px;
}

.error {
  position: absolute;
  display: block;
  flex-direction: column;
  align-items: center;

  font-weight: 700;
  font-style: normal;
  font-size: 32px;
  color: rgb(247, 15, 18);
  text-align: center;

  width: 700px;
  height: 100px;
  margin-top: 50px;
  margin-left: 100px;
}

.error::before {
  content: '';
  width: 15px;
  height: 15px;
  border-radius: 50%;
  display: block;
  margin: 15px auto;
  position: relative;
  color: rgb(247, 15, 18);
  box-sizing: border-box;
  animation: animloader 1s linear infinite alternate;
}

@keyframes animloader {
  0% {
    box-shadow: -38px -12px, -14px 0, 14px 0, 38px 0;
  }
  33% {
    box-shadow: -38px 0px, -14px -12px, 14px 0, 38px 0;
  }
  66% {
    box-shadow: -38px 0px, -14px 0, 14px -12px, 38px 0;
  }
  100% {
    box-shadow: -38px 0, -14px 0, 14px 0, 38px -12px;
  }
}

.error::after {
  content: '';
  display: block;
  width: 700px;
  height: 700px;
  background-image: url('https://papik.pro/grafic/uploads/posts/2023-04/thumbs/1682206904_papik-pro-p-stikeri-rizhii-kot-vektor-6.jpg');
  background-repeat: no-repeat;
  margin-left: 100px;
}

.breed-select {
  position: static;
  display: block;
  width: 700px;
  height: 60px;

  font-weight: 700;
  font-style: normal;
  font-size: 28px;
  color: #1c0c42;
  /* outline: transparent; */
  /* outline-style: none; */
  border: 2px #211342 solid;

  margin-left: 100px;
  margin-right: 100px;
  margin-top: 10px;
}

.cat-info {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: baseline;

  width: 700px;
  height: 800px;

  margin-left: 100px;
  margin-right: 100px;
}

.picture {
  display: block;
  width: 700px;
  height: 600px;
  object-fit: cover;
}

.title {
  font-size: 38px;
  margin-bottom: 0;
}

.description {
  font-size: 24px;
}

.temperament {
  font-size: 28px;
  margin-top: 0;
}
