.loader {
  color: #3b2d5a;
  text-align: center;
  flex-direction: column;
  align-items: center;
  width: 700px;
  height: 100px;
  margin-top: 50px;
  margin-left: 100px;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  display: block;
  position: absolute;
}

.loader:before {
  content: "";
  color: #3b2d5a;
  box-sizing: border-box;
  border-radius: 50%;
  width: 15px;
  height: 15px;
  margin: 15px auto;
  animation: 1s linear infinite alternate animloader;
  display: block;
  position: relative;
}

@keyframes animloader {
  0% {
    box-shadow: -38px -12px, -14px 0, 14px 0, 38px 0;
  }

  33% {
    box-shadow: -38px 0, -14px -12px, 14px 0, 38px 0;
  }

  66% {
    box-shadow: -38px 0, -14px 0, 14px -12px, 38px 0;
  }

  100% {
    box-shadow: -38px 0, -14px 0, 14px 0, 38px -12px;
  }
}

.loader:after {
  content: "";
  background-image: url("https://papik.pro/grafic/uploads/posts/2023-04/1682206914_papik-pro-p-stikeri-rizhii-kot-vektor-26.png");
